html {
    box-sizing: border-box;
    font-size: 100%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: $white;
    color: $black;
    font-family: 'century-gothic', sans-serif;
}

ul li {
    list-style-type: none;
}

a {
    color: $black;
    text-decoration: none;
}

.web-dev {
    filter: grayscale(100%);
    transition: all .4s;
}

.web-dev:hover {
    filter: grayscale(0%);
    transition: all;
}

// UI elements
.line {
    background-color: $accent;
    width: 75px;
    height: 2px;
    opacity: .7;
}